<style lang="sass">
  @import './index.scss';
</style>
<template>
  <span class="button" :class="`${type} ${className} ${size}`" @click="$emit('onClick')">
    <slot></slot>
  </span>
</template>
<script>
  /**
   *  @property {String} type = 'primary'  may be `angular` | `ghost`
   *  @property {String} size = 'medium'  may be `small` | `large`
   */

  import { transStyleObjectToString } from '@/utils'

  export default {
    name: 'Button',
    props: {
      className: {
      	type: String,
        default: ''
      },
      type: {
        type: String,
        default: 'primary'
      },
      size: {
        type: String,
        default: 'medium'
      }
    }
  }
</script>




