import kebabCase from 'lodash/kebabCase'
import isEmpty from 'lodash/isEmpty'
/**
 * 去除传入字符串中HTML标签，下面的方法未使用innerHTML 的原因是出于安全
 * * 说明
 *    1. 对于可执行元素的返回结果同innerHTML 不同，该方法只是把标签字符串去掉；
 *    2. 如果元素内的字符包含 `< ...>` 或者 `</ .... >` 类的字符也会去除
 *
 * * Note: 返回的内容会去掉所有的空格和空行
 * @param str
 */
export function stripHtmlTag ( str ) {
  if ( typeof str !== 'string' ) {
    return ''
  }

  return str.replace(/\s/g, '').replace(/<[^>]+>|<\/[^>]+>]/g, '')
}

/**
 * 向指定节点后面插入一个节点
 * @param {HTMLElement} node 插入节点
 * @param {HTMLElement} elem 参考节点
 *
 * @return node
 */
export function insertAfter ( node, elem ) {
  if ( isEmptyObject(elem) || isEmptyObject(elem.parentElement) ) {
    return node
  }

  const
    parentNode = elem.parentElement,
    nodes = Array.from(parentNode.childNodes),
    idx = nodes.findIndex(it => it === elem)

  if ( nodes.length !== idx + 1 ) {
    parentNode.insertBefore(node, nodes[idx + 1])
  } else {
    parentNode.appendChild(node)
  }

  return node
}

