/**
 * 从一个指定对象中获取指定的key 所对应的数据，始终返回一个数组；如果找不到返回空数组
 * @param {Object|*} obj 任意数据结构，只能保证不抛异常
 * @param {String} path 取值属性路径
 * @param {Boolean} [onlyOne] (optional)  取到其中一个值就返回
 *
 * @return {Array}
 */
export function getAnyKeyValuesFromObject ( obj, path, onlyOne ) {
  if ( typeof path !== 'string' ) {
    throw new Error('\'getAnyKeyValuesFromObject\': The second parameter must be a string')
  }

  let
    parts = path.replace(/\s/g, '').split(/\.|(?=\[.*])/),
    ret = []
  const
    // 这里其实也可以直接使用eval， 不需要使用循环；考虑向后兼容以及官方建议，这里不使用eval 实现；
    getValue = ( obj, pathArr ) => {
      for ( let i = 0, len = pathArr.length; i < len; ++i ) {
        if ( obj === undefined || (!isObject(obj) && !isArray(obj)) ) {
          return undefined
        }
        if ( isArray(obj) ) {
          if ( pathArr[i] === '[]' ) {
            const arrRet = obj.map(
              it => {
                return getValue(it, pathArr.slice(i + 1))
              }
            ).filter(
              it => {
                return it !== undefined
              }
            )

            return isEmptyObject(arrRet) ? undefined : arrRet
          }

          obj = obj[(pathArr[i].match(/\[(.+)]/) || [])[1] || pathArr[i]]
        } else {
          obj = obj[pathArr[i]]
        }
      }

      return obj
    },
    collectValues = ( o, pArr ) => {
      let v = getValue(o, pArr)

      v !== undefined && ret.push(v)

      if ( onlyOne === true && ret.length ) {
        return true
      }
      for ( let p in o ) {
        if ( o.hasOwnProperty(p) && (isObject(o[p]) || isArray(o[p])) ) {
          collectValues(o[p], pArr)
        }
      }
    }

  collectValues(obj, parts)

  return onlyOne ? ret[0] : ret
}

export function clearAllCookie ( domain, path ) {
  const allDomain = getAllDomain(domain)

  // 删除所有主域和子域cookie;
  Object.keys(getAllCookie()).forEach(it => allDomain.forEach(subDomain => clearCookie(it, subDomain, path)))
}

export function getAllDomain ( domain ) {
  let ret = ['']
  const subPartArr = ((domain || (document.domain.match(/(www\.|^)(.+)/) || [])[2]) || '').split('.')

  for ( let i = 0, len = subPartArr.length; i < len - 1; ++i ) {
    ret.push(subPartArr.slice(i).join('.'))
  }

  return ret
}

export function getAllCookie () {
  let allCookies = {}

  document.cookie.split('; ').forEach(
    it => {
      let item = it.split('=') || []

      allCookies[decodeURIComponent(item[0])] = item[1] ? decodeURIComponent(item[1]) : ''
    }
  )

  return allCookies
}

export function hasClass ( target, className ) {
  return isObject(target)
         && String(target.className).search(new RegExp('(^|\\s)' + className + '(\\s|$)')) !== -1
}

export function removeClass ( target, className ) {
  hasClass(target, className)
  && (target.className = String(target.className).replace(new RegExp('(^|\\s)' + className + '(\\s|$)'), ' '))

  return target
}

export function addClass ( target, className ) {
  !hasClass(target, className)
  && isObject(target)
  && (target.className = (target.className || '') + ' ' + className)

  return target
}

/**
 * 判断某元素的父元素（包含当前元素，不包容器元素）是否包含指定class样式；
 * 如果包含返回true, 否则返回false;
 *
 * @param target { HTMLElement } 起始节点
 * @param className { string } 要查找的class 名字
 * @param container {HTMLElement} (optional) container = document.body 指定查找容器，默认为document.body
 *
 * @return {boolean}  有指定class 返回true, 否则返回FALSE；
 */
export function parentHasClass ( target, className, container = document.body ) {
  if ( container.contains(target) ) {
    let temp = target,
      has = false

    while ( temp !== container ) {
      has = hasClass(temp, className)

      if ( has ) {
        return has
      }

      temp = temp.parentNode
    }
  }

  return false
}

export function closest ( target, className ) {
  while ( target && target !== document ) {
    if ( hasClass(target, className) ) {
      return target
    }

    target = target.parentNode
  }

  return null
}


export function isArray ( target ) {
  return Object.prototype.toString.call(target) === '[object Array]'
}

/**
 * Decide the parameter is an empty object
 *
 * *Note
 *      If the parameter is object but not a plain-object ,it will return `false`
 *
 *          |   type                |   return |
 *          |-----------------------|----------|
 *          |   Number              |   true   |
 *          |   String              |   true   |
 *          |   null                |   true   |
 *          |   undefined           |   true   |
 *          |   non-plain-object    |   false  |
 *          |   plain-object without prop    |  true   |
 *          |   empty-Array         |   true    |
 * @param {*} obj
 * @returns {boolean}
 */
export function isEmptyObject ( obj ) {
  if ( typeof obj === 'string' ) {
    return true
  }

  for ( let p in obj ) {
    if ( obj.hasOwnProperty(p) ) {
      return false
    }
  }

  if ( isObject(obj) && !isPlainObject(obj) ) {
    return false
  }

  return true
}

export function isFunction ( target ) {
  return Object.prototype.toString.call(target) === '[object Function]'
}

export function isObject ( target ) {
  return typeof target === 'object' && !isArray(target) && target !== null
}

export function isPlainObject ( obj ) {

  // 如果是继承对象，在对象构造函数的原型 中是没有对象的基础方法的；包括自定义对象，window 对象，DOM 对象
  return isObject(obj) && obj.constructor.prototype.hasOwnProperty('hasOwnProperty')
}

export function extend ( target = {}, ...sources ) {
  let deep = false,
    i = 0

  if ( typeof target === 'boolean' ) {
    deep = target

    target = sources[i++] || {}
  }

  if ( typeof target !== 'object' ) {
    target = {}
  }

  if ( !deep ) {
    return Object.assign(target, ...sources)
  }

  for ( let src, copy, source, p, len = sources.length; i < len; i++ ) {
    if ( ( source = sources[i] ) == null ) {
      continue
    }

    for ( p in source ) {
      src = target[p]
      copy = source[p]

      if ( Object.is(src, copy) ) {
        continue
      }

      // Case Array
      if ( isArray(copy) ) {
        target[p] = extend(deep, isArray(src) ? src : [], copy)
      }
      // Case Object
      else if ( isPlainObject(copy) ) {
        target[p] = extend(deep, isPlainObject(src) ? src : {}, copy)
      } else if ( copy !== undefined ) {
        target[p] = copy
      }
    }
  }

  return target
}

export function serialize ( obj ) {
  let param = []
  const
    // 这里的v 如果为undefined 则转成空字符串；
    eCode = ( k, v ) => encodeURIComponent(k) + "=" + (v === undefined ? '' : encodeURIComponent(v)),
    sArr = ( key, vs ) => vs.map(n => eCode(key, n)).join("&")

  for ( let p in obj ) {
    if ( obj.hasOwnProperty(p) ) {
      isArray(obj[p]) ? param.push(sArr(p, obj[p]))
        : param.push(eCode(p, obj[p]))
    }
  }

  return param.join("&").replace(/%20/g, "+")
}

export function dateFormat ( utc, f ) {
  if ( !+utc ) {
    return ""
  }

  let cDate = new Date(utc),
    preZero = function ( num ) {
      return num < 10 ? "0" + num : num
    }

  let fy = cDate.getFullYear(),
    m = cDate.getMonth() + 1,
    d = cDate.getDate(),
    h = cDate.getHours(),
    minutes = cDate.getMinutes(),
    sec = cDate.getSeconds()


  return f.replace(
    /(yyyy|yy)|(OO)|(dd)|(hh)|(mm)|(ss)/ig, function ( site ) {

      switch ( site.substr(0, 1).toLowerCase() ) {
        case "y" :
          return String(fy).slice(-site.length)
        case "o" :
          return site.length >= 2 ? preZero(m) : m
        case "d" :
          return site.length >= 2 ? preZero(d) : d
        case "h" :
          return site.length >= 2 ? preZero(h) : h
        case "m" :
          return  (site.length >= 2 ? preZero(minutes) : minutes)
        case "s" :
          return site.length >= 2 ? preZero(sec) : sec

        // 无匹配制定格式，返回元字符串；
        default:
          return site
      }
    }
  )
}

export const noop = () => {
}

export const toThousands = ( v ) => {
  return String(v).replace(
    /^(\d+)((\.\d+)?)$/, function ( v1, v2, v3 ) {
      return v2.replace(/\d{1,3}(?=(\d{3})+$)/g, '$&,') + (v3 || '')
    }
  )
}

export const parseJson = ( str ) => {
  try {
    return JSON.parse(str || '{}')
  } catch ( e ) {
    return {}
  }
}

/**
 * 传入一个数值或者带单位的文件大小值，转成指定单位的尺寸默认自动选择适当尺寸；保留2位小数
 *
 * @param {Number|String} size 可以是一个数值或者带单位的字符串, 如果该值未带单位则默认为`B`
 * @param {String} [unit] (optional) 可以是`B`, `KB` , `MB`, `GB`
 */
export const getSizeWithUnit = ( size = 0, unit ) => {
  const
    unitMap = {
      B: 1,
      K: Math.pow(2, 10),
      M: Math.pow(2, 20),
      G: Math.pow(2, 30)
    },
    mArr = String(size).match(/(^[\d.]+)([kmg]?)/i),
    receiveUnit = mArr[2].toUpperCase() || 'B',
    bitSize = mArr[1] * unitMap[receiveUnit]

  if ( unit ) {
    return bitSize / unitMap[unit[0].toUpperCase()] + unit
  }

  if ( bitSize >= unitMap.G ) {
    return (bitSize / unitMap.G).toFixed(2) + 'GB'
  } else if ( bitSize >= unitMap.M ) {
    return (bitSize / unitMap.M).toFixed(2) + 'MB'
  } else if ( bitSize >= unitMap.K ) {
    return (bitSize / unitMap.K).toFixed(2) + 'KB'
  }

  return bitSize + 'B'
}

const toCamelCase = t => t.replace(
  /^[A-Z]|(([_-]+)|(\d+))(.)/g,
  ( ...args ) => args[1] === undefined ? args[0].toLowerCase() : ((args[3] || '') + args[4].toUpperCase())
)


export function fenToYuan ( num ) {
  return ((Math.floor(num) || 0) / 100).toFixed(2)
}

let __idStart = 0

export function getUniqueId ( prefix = 'U' ) {
  return prefix + '_' + (+new Date).toString(36) + __idStart++
}

export default  {
  getUniqueId,
  noop,
  extend
}

export function transStyleObjectToString ( sObj ) {
  let ret = ''

  if ( typeof sObj === 'string' ) return sObj
  if ( isEmpty(sObj) ) return ''

  for ( let p in sObj ) {
    ret += `${kebabCase(p)}:${sObj[p]};`
  }

  return ret
}


/**
 * Get an unique string map Object with you given it string
 * @param {String|Array} args
 * @returns {Object}
 */
export function getUniqueKeyMap ( ...args ) {
  let retMap = {}

  args.forEach(
    it => {
      if ( typeof it === 'string' ) {
        retMap[it] = getUniqueId(it)
      } else if ( Object.prototype.toString.call(it) === '[object Array]' ) {
        Object.assign(retMap, getUniqueKeyMap.apply(null, it))
      } else {
        throw( new Error('each argument into "getUniqueKeyMap" must be an Array or String'))
      }
    }
  )

  return retMap
}

export function getChineseDay(date) {
  const days = ['日', '一', '二', '三', '四', '五', '六'];
  return `周${days[date.getDay()]}`;
}

/**
 * Date => '1月1日 今天|明天|后天|周一'
 * @param {Date} date
 */
export function toChineseDate(date) {
  let today = new Date();
  let tomorrow = (new Date(+today + 24 * 60 * 60 * 1000));
  let dayAfterTomorrow = (new Date(+today + 2 * 24 * 60 * 60 * 1000));

  const isSameDay = (day, compareWith) =>
    day.getFullYear() === compareWith.getFullYear() &&
    day.getMonth() === compareWith.getMonth() &&
    day.getDate() === compareWith.getDate();

  const month = date.getMonth() + 1;
  const dateOfMonth = date.getDate();
  let dayOfWeek = getChineseDay(date);

  if (isSameDay(date, today)) {
    dayOfWeek = '今天';
  } else if (isSameDay(date, tomorrow)){
    dayOfWeek = '明天';
  } else if (isSameDay(date, dayAfterTomorrow)) {
    dayOfWeek = '后天';
  }

  return `${month}月${dateOfMonth}日 ${dayOfWeek}`;
}



/**
 * 获取url上的参数
 * @param str
 * @returns {{}}
 */
export function getParams (str = '') {
  const result = {}
  const kvArr = str.split('&')
  let tmp

  kvArr.forEach(kv => {

    tmp = kv.split('=')
    result[decodeURIComponent(tmp[0])] = decodeURIComponent(tmp[1])
  })

  return result
}



