import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Home/index'
import Down from '@/components/Down/index'

Vue.use(Router)

export default new Router(
  {
    routes: [{
      path: '/',
      name: 'Index',
      component: Index,
    },
      {
        path: '/down',
        name: 'Down',
        component: Down,
      }
    ]
  })



