<template>
    <render-to-layer :show="show" className="modal-backdrop">
        <slot></slot>
    </render-to-layer>
</template>

<script>
export default {
    props: {
        show: { type: Boolean, required: true, default: false }
    }
}
</script>

<style lang="scss">
.modal-backdrop {
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>



