<style lang="sass" scoped>
  @import './index.scss'
</style>
<style lang="scss">
  @import './public.scss';
</style>
<template>
  <div class="container h-100 fz-12px">
    <div class="w-100 p-15px pos-a form-container">
      <section class="p-10px bor-r-6px w-100 fz-12px wrap" style="bottom: 0; left: 0">
        <div class="field-item">
          <p class="smartphone"/>
          <mt-field placeholder="请输入注册手机号" class="d-ib" type="number" v-model="formData.phone" disableClear>
          </mt-field>
        </div>
        <div class="field-item">
          <p class="a1b2"/>
          <mt-field placeholder="请输入图形验证码" class="d-ib" type="text" v-model="formData.imgcode" disableClear>
            <Verify @getImgCode="getImgCodeUrl" ref="vueImgVerify" width="60px" height="30px"></Verify>
          </mt-field>
        </div>
        <div class="field-item">
          <p class="shield"/>
          <mt-field placeholder="请输入验证码" class="d-ib" type="number" v-model="formData.iphonecode" disableClear>
            <p class="validate-code-iphone" @click="getPhoneCode" v-bind:class="{'disabled': !!isDisabled }">
              <span v-if="!!isDisabled">重新发送({{isDisabled}})</span>
              <span v-if="!isDisabled">获取验证码</span>
            </p>
          </mt-field>
        </div>
        <div class="field-item">
          <p class="password"/>
          <mt-field placeholder="请设置由6-16位数字和字母组成的密码" class="d-ib password-input" type="password" disableClear
                    v-model="formData.password"></mt-field>
        </div>
        <div class="field-item">
          <p class="person"/>
          <span class="mint-cell mint-field">
            <span class="mint-cell-wrapper">
              推荐人
              <span class="ml-10px">
                {{inviteName}}
              </span>
            </span>
          </span>
        </div>
        <div class="register-btn">
          <mt-button type="danger" size="small" @click.native="handleRegisterClick">注册体验&下载</mt-button>
        </div>

      </section>
    </div>
    <div class="download-app" @click="handleDownClick">
      已注册？ 直接下载APP
    </div>
  </div>
</template>
<script>
  import fetch from '../../utils/fetch'
  import { getParams } from '../../utils/index'
  import { GET_CHECK_CODE, REGISTER } from '../../constants/api'
  import { MessageBox, Toast } from 'mint-ui'
  import Verify from '../../components/Base/VueImgVerify.vue'

  export default {
    name: 'Home',
    metaInfo: {
      title: '注册'
    },
    components: {
      Verify
    },
    data() {
      return {
        formData: {
          phone: '',
          imgcode: '',
          iphonecode: '',
          password: ''
        },
        isDisabled: 0,
        inviteName: '',
        isLoading: false
      }
    },
    mounted() {
      this.inviteName = this.getParams().name
    },
    methods: {
      getParams() {
        const paramArr = location.search.split('?') || []
        const paramStr = paramArr[paramArr.length - 1]
        return getParams(paramStr) || {}
      },
      getImgCodeUrl( res ) {
        this.imgCode = res
      },
      async getPhoneCode() {
        const { phone } = this.formData
        if ( this.validatePhone(phone) && this.isDisabled === 0 ) {
          const body = {
            mobilenumber: this.formData.phone,
            useobject: 1
          }
          const res = await fetch(GET_CHECK_CODE, {
            method: 'POST',
            dataType: 'json', // res
            body,
          })
          if ( res.status === '00' ) {
            this.setIntervalCode()
            Toast('获取验证码成功')
          }
        }
      },
      setIntervalCode() {
        this.isDisabled = 60
        const timer = setInterval(() => {
          if ( this.isDisabled === 0 ) {
            clearInterval(timer)
          } else {
            this.isDisabled--
          }
        }, 1000)
      },
      handleDownClick() {
        MessageBox.confirm(' ', '您已注册，点击前往下载', {
          cancelButtonText: '换号注册'
        }).then(res => {
          if ( res === 'confirm' ) {
            this.$router.push('/down');
          }
        })
      },
      validatePhone( phone ) {
        if ( !/^[\d]{11}$/.test(phone) ) {
          Toast('请输入正确的手机号')
          return false
        }
        return true
      },
      validateFormData() {
        const { phone, password, imgcode = '' } = this.formData
        if ( !this.validatePhone(phone) ) {
          return false
        } else if ( this.imgCode.toLocaleUpperCase() !== imgcode.toLocaleUpperCase() ) {
          Toast('请输入正确的图形验证码')
          return false
        } else if ( !/(?=.*[a-zA-Z])(?=.*[\d])[\w\W]{6,16}/.test(password) ) {
          Toast('请输入由6-16位数字和字母组成的密码')
          return false
        }
        return true
      },
      async handleRegisterClick() {
        if ( this.isLoading ) {
          return
        }
        if ( this.validateFormData() ) {
          this.isLoading = true
          let body = {
            mobilenumber: this.formData.phone,
            verifycode: this.formData.iphonecode,
            password: this.formData.password
          }
          if ( this.getParams().id ) {
            body.invitecode = this.getParams().id
          }
          const res = await fetch(REGISTER, {
            method: 'POST',
            dataType: 'json',
            body
          })
          this.isLoading = false;
          if ( res.status === '00' ) {
            this.registerSuccess()
          }
        }
      },
      registerSuccess() {
        MessageBox.alert('  ', '注册成功').then(res => {
          window.location.href = '/down'
        })
      }
    }
  }
</script>




