<template>
    <render-to-layer :show="show">
        <div class="slide-up-panel slide-up" :class="className">
            <slot name="header">
                <div class="panel-header">
                    <a href="javascript:;" class="cancel" @click="$emit('cancel')">取消</a>
                    <a href="javascript:;" class="ok" @click="$emit('confirm')">确定</a>
                </div>
            </slot>
            <div class="panel-body">
                <slot></slot>
            </div>
            <slot name="footer"></slot>
        </div>
    </render-to-layer>
</template>

<script>
export default {
    props: {
        show: { type: Boolean, required: true, default: false },
        className: { type: String, default: '' }
    }
}
</script>

<style lang="scss">
.slide-up-panel {
    position: fixed;
    width: 100%;
    max-height: 450px;
    left: 0;
    bottom: 0;
    background: #fff;
    display: flex;
    flex-direction: column;
}

.panel-header {
    display: flex;
    flex-shrink: 0;
    height: 40px;
    border-bottom: 1px solid #E9ECF0;
    justify-content: space-between;

    .cancel,
    .ok {
        padding: 0 15px;
        text-decoration: none;
        font-size: 16px;
        line-height: 40px;
    }

    .cancel {
        color: #262A30;
    }
}

.panel-body {
    overflow: auto;
}

.panel-footer {
    display: flex;

    .button {
        height: 54px;
        line-height: 54px;
        flex-grow: 1;
        border: 0;
        border-radius: 0;
    }
}

.panel-header.has-title {
    display: block;
    text-align: center;
    line-height: 40px;
    font-size: 16px;

    .close-btn {
        position: absolute;
        right: 0;
        top: 0;
        text-decoration: none;
        width: 40px;
        height: 40px;
        font-size: 28px;
        color: #262A30;
    }
}
</style>



