import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import './assets/css/index.scss'
import Meta from 'vue-meta'
import store from './store/'
import baseComponents from '@/components/Base/';
import { Button, Field } from 'mint-ui'

Vue.config.productionTip = false
Vue.use(Meta)
Vue.component(Button.name, Button)
Vue.component(Field.name, Field)

if ( process.env.NODE_ENV !== 'production'  ) {
  const vConsole = require('vconsole')
  const vCIns = new vConsole()

  console.log('----init v-console---')
}

// righister global components
// TODO: consider auto registration
// https://vuejs.org/v2/guide/components-registration.html#Automatic-Global-Registration-of-Base-Components
for ( let component in baseComponents ) {
  Vue.component(component, baseComponents[component]);
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



