<script>
import Vue from 'vue';
import store from '@/store/';

export default {
    props: {
        show: { type: Boolean, required: true, default: false },
        className: { type: String, default: '' }
    },

    mounted() {
        this._el = document.createElement('div');
        document.body.appendChild(this._el);

        this._vm = new Vue({
            // abstract: true,
            name: 'portal-component',
            el: this._el,
            // parent: this,
            render: createElement => {
                return createElement('transition', {
                    attrs: { name: 'layer-mask', appear: true }
                }, [
                    this.show && 
                        createElement('div', {
                            class: `layer-mask ${this.className}` 
                        }, [this.$slots.default])
                ]);
            }
        });
    },

    // won't cause update on vm protal, do force update
    updated() {
        this._vm && this._vm.$forceUpdate();
    },

    beforeDestroy() {
        const _vm = this._vm;
        if (_vm) {
            _vm.$destroy();
            _vm.$el.remove();
            delete this._vm;
            delete this._el;
        }
    },

    render: h => null
}
</script>

<style lang="scss">
.layer-mask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .6);
}

.layer-mask-enter,
.layer-mask-leave-to {
    background-color: rgba(0, 0, 0, 0);

    .slide-up {
        transform: translateY(100%);
    }
}

.layer-mask-enter-active,
.layer-mask-leave-active {
    transition: background-color .2s;

    .slide-up {
        transition: transform .2s;
    }
}
</style>



