<style lang="sass" scoped>
  @import './index.scss'
</style>
<style lang="scss">
  @import './public.scss';
</style>
<template>
  <div class="container-down h-100">
    <div class="register-btn">
      <mt-button type="primary" size="small" @click.native="handleIosClick"><p class="ios-btn"/>下载iOS客户端
      </mt-button>
      <mt-button type="primary" size="small" @click.native="handleAndroidClick"><p class="android-btn"/>下载Android客户端
      </mt-button>
    </div>
    <div v-bind:class="{'mask': isShowMask }"/>
  </div>
</template>
<script>

  export default {
    name: 'Down',
    metaInfo: {
      title: '下载'
    },
    components: {},
    data() {
      return {
        isShowMask: false,
      }
    },
    methods: {
      isWX() {
        const ua = navigator.userAgent.toLowerCase();
        return (/micromessenger/.test(ua)) ? true : false;
      },
      handleAndroidClick() {
        if ( this.isWX() ) {
          this.isShowMask = true
        } else {
          location.href = '//fir.im/1zcw'
        }
      },
      handleIosClick() {
        location.href = '//fir.im/dg42'
      }
    }
  }
</script>




