import { isObject, serialize, extend, dateFormat } from './index'
import { Toast } from 'mint-ui'
import MD5 from 'blueimp-md5'

const key = '8c23828c52464800'

// As soon as possible TODO: 优化fetch 封装
function sort_ASCII( obj ) {
  var arr = new Array();
  var num = 0;
  for ( var i in obj ) {
    arr[num] = i;
    num++;
  }
  var sortArr = arr.sort();
  var sortObj = {};
  for ( var i in sortArr ) {
    sortObj[sortArr[i]] = obj[sortArr[i]];
  }
  return sortObj;

}

export default function ( input, init = {} ) {

  init.body.usercode = 100001
  init.body.timespan = dateFormat(+new Date(), 'yyyyOOddhhmmss')
  const querystring = serialize(sort_ASCII(init.body))
  init.body.sign = MD5(MD5(key).toLocaleUpperCase() + querystring).toLocaleUpperCase()

  return new Promise(
    ( resolve, reject ) => {
      let tempOption = {}

      if ( isObject(init.body) ) {
        if ( /\s*POST\s*/i.test(init.method) ) {
          init.method = 'POST';
          tempOption.body = getContentTypeData(init.contentType || 'form', init.body);
        } else if ( !init.method && typeof input === 'string' ) {
          input += (input.indexOf('?') !== -1 ? '' : '?') + getContentTypeData('form', init.body)
        }
      }

      // TODO: 如果body 传递其它格式情况下的get 和 post 参数设定；
      fetch(
        input,
        extend(
          true, {
            headers: {
              'Content-Type': init.contentType === 'json' ?
                'application/json; charset=utf-8' :
                'application/x-www-form-urlencoded; charset=UTF-8'
            },
            cache: 'default',
            // credentials: 'include',
            method: 'GET'
          },
          init,
          tempOption
        )
      ).then(res => {
        if ( !res.ok ) throw new Error('NETWORK ERROR');
        return (!init.dataType || init.dataType === 'json') ? res.json() : res.text();
      }).then(res => {
        if ( typeof res === 'object' && res.status !== '00' ) {
          Toast(res.msg || '返回失败，请重新尝试');
        }
        resolve(res);
      }).catch(err => {
        Toast('请求异常，请重新尝试');
        console.error(err);
        resolve(null);
      });
    }
  )
}

function getContentTypeData( type, data ) {
  switch ( type.toUpperCase() ) {
    case 'JSON' :
      return JSON.stringify(data);
    case 'FORM' :
      return serialize(data);

    default:
      return data;
  }
}



